nav {
    text-align: end;
    background-color: red;
    padding: 0.1rem;
    line-height: 50px;
    position: fixed;
    right: 0;
    left: 0;
    z-index: 9;
    height: 50px;
    top: 0;
}

nav>ol>li {
    display: inline-block;
    padding: 0.2rem;
    height: 50px;
    margin-left: 2px;
}

nav>ol>li>a {
    text-decoration: none;
    font-weight: bold;
    color: white;
}

.container {
    position: absolute;
    top: 50px;
}

.mobile_toggle_menu {
    display: none;
}

.mobile_toggle_menu>li {
    color: white;
    font-weight: bold;
    list-style-type: none;
    font-size: xxx-large;
}

.logoContainer {
    width: 50px;
    height: 50px;
    display: inline-block;
    position: absolute;
    left: 0;
    z-index: 1;
    top: 0;
    ;
}

#pageSumarry {
    display: none;
}

@media only screen and (max-width: 600px) {

    .mobile_toggle_menu {
        display: inline-block;
        line-height: 42px;
        background: none;
        border: none;
        z-index: 10;
        position: absolute;
        right: 15px;
    }

    .menuContainer {
        position: absolute;
        background-color: red;
        right: -120%;
        top: 0;
        left: 0;
        bottom: 0;
        transition: right 500ms;
    }

    .show_mobile {
        background-color: red;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        height: 100vh;
    }

    nav>ol>li {
        display: block;
    }

    nav>ol.show_mobile {
        padding-top: 70px;
    }

    nav {
        text-align: center;
        justify-content: center;
        display: flex;
    }


    #pageSumarry {
        display: inline;
        width: auto;
        z-index: 8;
        position: absolute;
        top: 0;
        color: white;
        font-weight: bold;
        font-size: 1.2rem;
    }
}